button {
  position: relative;
  display: inline-block;
  cursor: pointer;
  outline: none;
  border: 0;
  vertical-align: middle;
  text-decoration: none;
  font-size: inherit;
  font-family: inherit;
}

.todoFormContaner {
  width: 380px;
  opacity: 1;
  position: absolute;
  /* top: 50%; */
  left: 50%;
  transform: translate(-50%, 50%);
  z-index: 10;
}

.todoFormBox {
  padding: 20px 20px 10px 20px;
  border-radius: 20px;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.093);
  animation: show 0.7s ease;
  background-color: white;
}

@keyframes show {
  0% {
    transform: scale(0) translateY(-50%);
    opacity: 0;
  }
  100% {
    transform: scale(1) translateY(0);
    opacity: 1;
  }
}

.todoBox * {
  margin: 0 auto;
  text-align: center;
}

.todoForm {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
}

.todoForm div:first-child {
  width: 100%;
  height: fit-content;
  text-align: center;
}

.input {
  font-size: 18px;
  border-radius: 6px;
  border: solid 1px #ccc;
  height: 55px;
  padding: 3px;
  text-align: center;
  margin-bottom: 10px;
  max-width: 330px;
  width: 100%;
}

.inputError {
  font-size: 18px;
  border-radius: 6px;
  border: solid 1px #ccc;
  height: 55px;
  padding: 3px;
  text-align: center;
  margin-bottom: 10px;
  max-width: 330px;
  width: 100%;
}

input::placeholder {
  color: #ccc;
  font-size: 18px;
  padding: 3px;
  text-align: center;
}

.inputError::placeholder {
  color: red;
  font-size: 18px;
  padding: 3px;
  text-align: center;
}

.todoBtn {
  width: 100%;
  height: 55px;
  font-size: 18px;
  padding: 5px;
  background-color: rgba(0, 157, 255, 0.773);
  color: white;
  border-radius: 6px;
  cursor: pointer;
  margin-bottom: 10px;
}

.todoBtn:disabled {
  background-color: rgba(37, 101, 131, 0.212);
}

.todoBtn:disabled:hover {
  background-color: #ccc;
  color: white;
  border: none;
  padding: 5px;
}

.todoBtn:disabled:active {
  transform: none;
  box-shadow: none;
}

.todoBtn:hover {
  color: rgb(37 102 131);
  background-color: white;
  border: solid 1px rgba(0, 157, 255, 0.773);
  padding: 4px;
}

.todoBtn:active {
  box-shadow: inset 2px 2px 2px rgba(0, 157, 255, 0.773);
  border: none;
  padding: 5px;
  transform: translate(1px, 1px);
}